import React, { Component } from "react";
import withStyles from "@material-ui/core/styles/withStyles";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import RuleCard from "../cards/RuleCard";
import MonthYear from "../menus/MonthYear";
import YearMenu from "../menus/YearMenu";
import TextField from "@material-ui/core/TextField";

const styles = theme => ({
  primary: {
    marginRight: theme.spacing.unit * 2
  },
  secondary: {
    background: theme.palette.secondary["100"],
    color: "white"
  },
  spaceTop: {
    marginTop: 20
  },
  button: {
    marginTop: 10,
    marginBottom: 5,
    color: "#000000"
  },
  container: {
    display: "flex",
    justifyContent: "left",
    flexWrap: "wrap",
    alignItems: "center",
    padding: theme.spacing.unit * 1
  },
  textField: {
    width: 100,
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    textAlign: "center"
  },
  rules: {
    color: "#3A3A3A",
    fontSize: "large"
  }  
});

function yearMonthString(v1) {
    var s1 = v1 + '月'
    if(v1 >= 12) s1 = (v1 / 12) + '年'
    return s1;    
}

class RuleListItem2 extends Component {
  state = {
      v1: "12",
      v2: "1",
      v3 : 1
  };

  v1Change = (value) => {
    this.setState({ v1: value })

  }

  v2Change = (value) => {
    this.setState({ v2: value })

  }

  v3Change = name => event => {
    this.setState({ [name]: event.target.value });
  };

  render() {
    const { classes } = this.props;
    const {
      v1, v2, v3
    } = this.state;

    let finalTitle = this.props.t1 
    + yearMonthString(v1) 
    + this.props.t2 
    + yearMonthString(v2) 
    + this.props.t3
    + v3
    + this.props.t4

    return (
          <Grid container spacing={24}>
            <Grid item xs={5}>
              <RuleCard
                header={this.props.header}
                title={finalTitle}
                desc={this.props.desc}
                tp={this.props.tp}
              />
            </Grid>

            <Grid item xs={7} className={classes.container}>
              <Typography className={classes.rules}>
                {this.props.t1}
              </Typography>
              <YearMenu v1Change={this.v1Change} v1={v1}/>
              <Typography className={classes.rules}>
                {this.props.t2}
              </Typography>
              <MonthYear monthChange={this.v2Change} selectedV={v2} />
              <Typography className={classes.rules}>
                {this.props.t3}
              </Typography>              
              <TextField
                id="outlined-number"
                value={v3}
                type="number"
                className={classes.textField}
                onChange={this.v3Change('v3')}
                InputLabelProps={{
                  shrink: true
                }}
                margin="none"
              />
              <Typography className={classes.rules}>
                {this.props.t4}
              </Typography>
            </Grid>
          </Grid>
    );
  }
}

export default withStyles(styles)(RuleListItem2);
