import React, { Component } from "react";
import withStyles from "@material-ui/core/styles/withStyles";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import RuleCard from "../cards/RuleCard";
import MonthYear from "../menus/MonthYear";
import YearMenu from "../menus/YearMenu";
import TextField from "@material-ui/core/TextField";

const styles = theme => ({
  primary: {
    marginRight: theme.spacing.unit * 2
  },
  secondary: {
    background: theme.palette.secondary["100"],
    color: "white"
  },
  spaceTop: {
    marginTop: 20
  },
  button: {
    marginTop: 10,
    marginBottom: 5,
    color: "#000000"
  },
  container: {
    display: "flex",
    justifyContent: "left",
    flexWrap: "wrap",
    alignItems: "center",
    padding: theme.spacing.unit * 1
  },
  textField: {
    width: 100,
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    textAlign: "center"
  },
  rules: {
    color: "#3A3A3A",
    fontSize: "large"
  }
});

class RuleListItem1 extends Component {

  constructor(props) {
    super(props);
    this.state = {
      amount: 2,
      my : "1"
    };
  }

  monthChange = (value) => {
    console.log(value);
    this.setState({ my: value })

  }

  handleNumChange = name => event => {
    this.setState({ [name]: event.target.value });

  };

  render() {
    const { classes } = this.props;
    const {
      amount,
      my
    } = this.state;

    var s = my + '月'
    if(my >= 12) s = (my / 12) + '年'
    let finalTitle = this.props.t1 + s + this.props.t2 + amount + this.props.t3

    return (
          <Grid container spacing={24}>
            <Grid item xs={5}>
              <RuleCard
                header={this.props.header}
                title={finalTitle}
                desc={this.props.desc}
                tp={this.props.tp}
              />
            </Grid>

            <Grid item xs={7} className={classes.container}>
              <Typography className={classes.rules}>
                {this.props.t1}
              </Typography>
              <MonthYear monthChange={this.monthChange} selectedV={my}/>
              <Typography className={classes.rules}>
                {this.props.t2}
              </Typography>
              <TextField
                id="outlined-number"
                value={amount}
                type="number"
                onChange={this.handleNumChange('amount')}
                className={classes.textField}
                InputLabelProps={{
                  shrink: true
                }}
                margin="none"
              />
              <Typography className={classes.rules}>
                {this.props.t3}
              </Typography>
            </Grid>
          </Grid>
    );
  }
}

export default withStyles(styles)(RuleListItem1);
