import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Button from "@material-ui/core/Button";

const styles = theme => ({
  button: {
    display: "block",
    marginTop: theme.spacing.unit * 2
  },

  formControl: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    minWidth: 100,
    textAlign: "center"
  }
});

class YearMenu extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      age: this.props.v1,
      open: false
    };
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
    this.props.v1Change(event.target.value)
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleOpen = () => {
    this.setState({ open: true });
  };

  render() {
    const { classes } = this.props;

    return (
      <form autoComplete="off">
        <FormControl className={classes.formControl}>
          <Select
            open={this.state.open}
            onClose={this.handleClose}
            onOpen={this.handleOpen}
            value={this.state.age}
            onChange={this.handleChange}
            inputProps={{
              name: "age",
              id: "demo-controlled-open-select"
            }}
          >
            <MenuItem value={12}>1年</MenuItem>
            <MenuItem value={24}>2年</MenuItem>
            <MenuItem value={36}>3年</MenuItem>
            <MenuItem value={48}>4年</MenuItem>
            <MenuItem value={60}>5年</MenuItem>
            <MenuItem value={72}>6年</MenuItem>
            <MenuItem value={84}>7年</MenuItem>
          </Select>
        </FormControl>
      </form>
    );
  }
}

YearMenu.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(YearMenu);
