class WinRate extends React.Component {
	state = {
	};

	handleChange = event => {
		// this.setState({ [event.target.name]: event.target.value });
	};

	handleClose = () => {};

	handleOpen = () => {};

	render() {
		const { classes } = this.props;
		var paper = classes.paper
		if(this.props.tp == 0) 
			paper = classes.redValue
		else if(this.props.tp == 1)
			paper = classes.greenValue
		else paper = classes.greenValue

		return (
			<Paper className={classes.paper}>
						<div className={classes.box}>
							<Typography
								style={{ textTransform: "uppercase", "color":"#000000" }}
								gutterBottom
							>
								{this.props.title}
							</Typography>
							<Typography 
								className={paper}
							variant="h1"
							gutterBottom>
								{this.props.rate}%
							</Typography>
						</div>
			</Paper>
		);
	}
}

WinRate.propTypes = {
	classes: PropTypes.object.isRequired
};

export default withStyles(styles)(WinRate);
